import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'

import Systems from '../components/pages/SystemsView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'systems',
      component: Systems
    },
  ]
})



